.menu__checkbox {
    display: none;
}

.menu__btn {
    background-color: #fff;
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    position: fixed;
    top: 4rem;
    right: 6rem;
    z-index: 2000;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.15);
    text-align: center;
    cursor: pointer;
}
@media only screen and (max-width: 56.25em) {
    .menu__btn {
        top: 4rem;
        right: 4rem;
    }
}
@media only screen and (max-width: 37.5em) {
    .menu__btn {
        top: 3rem;
        right: 3rem;
    }
}

.menu__background {
    height: 5.5rem;
    width: 5.5rem;
    border-radius: 50%;
    position: fixed;
    top: 4.3rem;
    right: 6.3rem;
    background-image: radial-gradient($primary-color, $tertiary-color);
    z-index: 1000;
    transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
@media only screen and (max-width: 56.25em) {
    .menu__background {
        top: 4.3rem;
        right: 4.3rem;
    }
}
@media only screen and (max-width: 37.5em) {
    .menu__background {
        top: 3.3rem;
        right: 3.3rem;
    }
}

.menu__nav {
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1500;
    opacity: 0;
    width: 0;
    transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.menu__list {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    list-style: none;
    text-align: center;
    width: 100%;
}

.menu__item {
    margin: 4rem;
}

.menu__link:link,
.menu__link:visited {
    display: inline-block;
    font-size: 2.5rem;
    font-weight: 300;
    padding: 1rem 2rem;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    border-bottom: 2px solid #fff;
    background-image: linear-gradient(
        120deg,
        transparent 0%,
        transparent 50%,
        #fff 50%
    );
    background-size: 250%;
    transition: all 0.5s;
}

.menu__link:hover,
.menu__link:active {
    background-position: 100%;
    color: $primary-color;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.35);
}

.menu__checkbox:checked ~ .menu__background {
    transform: scale(120);
}

.menu__checkbox:checked ~ .menu__nav {
    opacity: 100;
    width: 100%;
}

.menu__icn {
    position: relative;
    margin-top: 3rem;
}
.menu__icn,
.menu__icn::before,
.menu__icn::after {
    width: 3rem;
    height: 2px;
    background-color: #777;
    display: inline-block;
}
.menu__icn::before,
.menu__icn::after {
    content: '';
    position: absolute;
    left: 0;
    transition: all 0.2s;
}
.menu__icn::before {
    top: -0.8rem;
}
.menu__icn::after {
    top: 0.8rem;
}

.menu__btn:hover .menu__icn::before {
    top: -1rem;
}

.menu__btn:hover .menu__icn::after {
    top: 1rem;
}

.menu__checkbox:checked + .menu__btn .menu__icn {
    background-color: transparent;
}

.menu__checkbox:checked + .menu__btn .menu__icn::before {
    transform: rotate(135deg);
    top: 0;
}

.menu__checkbox:checked + .menu__btn .menu__icn::after {
    transform: rotate(-135deg);
    top: 0;
}
