.content {
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    top: -5%;
    left: -5%;

    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(10, 10vh);
    grid-column: 1 / -1;
    grid-row: 1 / -1;

    &__item {
        position: relative;

        display: grid;
        justify-self: center;
        align-self: center;

        &-img {
            position: relative;
            transition: translate 1s;

            @media only screen and (max-width: $bp-largest) {
                width: 100%;
            }
        }
    }

    .item1 {
        grid-column: 9 / 11;
        grid-row: 2 / 4;
        animation: position-item1 1.3s cubic-bezier(0.27, 0.73, 0.67, 1.49),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (max-width: $bp-large) {
            grid-row: 4 / 6;
            grid-column: 10 / 12;
            z-index: -3;
        }
    }

    .item2 {
        grid-column: 7 / 9;
        grid-row: 8 / 10;

        animation: position-item2 1.7s cubic-bezier(0.41, 0.75, 0, 1.13),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (max-width: $bp-large) {
            grid-row: 9 / 11;
            grid-column: 6/8;
        }
        @media only screen and (min-height: 1200px) {
            grid-row: 6 / 8;
        }
    }

    .item3 {
        grid-column: 4 / 6;
        grid-row: 3 / 5;
        z-index: -3;

        animation: position-item3 0.7s cubic-bezier(0.35, 1.17, 0.83, 1.05),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (min-width: $bp-largest) {
            grid-column: 4 / 6;
        }

        @media only screen and (max-width: $bp-large) {
            grid-row: 2 / 4;
            grid-column: 6 / 8;
        }
    }

    .item4 {
        grid-column: 3 / 5;
        grid-row: 7 / 9;
        z-index: -5;

        animation: position-item4 1.1s cubic-bezier(0.35, 1.17, 0.83, 1.05),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (max-width: $bp-large) {
            grid-row: 6 / 8;
            grid-column: 3 / 5;
        }
    }

    .item5 {
        grid-column: 10 / -1;
        grid-row: 7 / 9;

        animation: position-item5 1.1s cubic-bezier(0.1, 1.09, 0.19, 1.46),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (max-width: $bp-large) {
            grid-column: 9 / 11;
        }
    }

    .item6 {
        grid-column: 2 / 4;
        grid-row: 4 / 6;

        animation: position-item6 1.1s cubic-bezier(0.1, 1.09, 0.19, 1.46),
            fadein 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);

        @media only screen and (max-width: $bp-large) {
            grid-row: 3 / 5;
        }
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes position-item1 {
    from {
        transform: translate(200px, -200px) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}

@keyframes position-item2 {
    from {
        transform: translate(0, 400px) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}
@keyframes position-item3 {
    from {
        transform: translate(-300px, -100px) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}
@keyframes position-item4 {
    from {
        transform: translate(-300px, 0) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}
@keyframes position-item5 {
    from {
        transform: translate(-50px, 50px) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}
@keyframes position-item6 {
    from {
        transform: translate(0, -400px) scale(0.1);
    }
    to {
        transform: translate(0, 0), scale(1);
    }
}
