.header {
    justify-self: center;
    grid-column: 1 / -1;
    grid-row: 4 / 6;

    &__title {
        font-size: 14vw;
        font-weight: 200;
        text-transform: uppercase;
        text-shadow: 0 1rem 1rem rgba(49, 49, 49, 0.5);
        color: rgba($color: #ffffff, $alpha: 0.9);
        animation: scalein 0.5s cubic-bezier(0.05, 0.96, 0.78, 0.67);

        @media only screen and (min-width: $bp-large) {
            font-size: 10vw;
        }
    }
}

@keyframes scalein {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
